<template lang="html">
  <div class="row">
    <div class="col-lg-8 col-md-8 col-sm-12">
      <div v-for="(group, index) of centerGroups">
        <form-card
          :group="group"
          v-model="form"
          @updateHandle="updateHandle"
        ></form-card>
        <div class="card" v-if="index == 0">
          <div class="card-header">
            <h6 class="title">Hnh nh</h6>
          </div>
          <div class="card-body form-card">
            <div class="row">
              <my-image
                multiple="true"
                v-model="form.list_image"
                :value="form.list_image"
                :feautureImage=form.image||1
                @changeFeautureImage="changeFeautureImage"
              ></my-image>
            </div>
          </div>
        </div>
        <div class="card" v-if="index == 0">
          <div class="card-header">
            <h6 class="title">Phin bn sn phm</h6>
          </div>
          <div class="card-body form-card">
            <div class="row">
              <div class="col-sm-12 col-lg-6 col-md-6 p-0">
                <div class="row m-0">
                  <el-switch
                    v-model="stock_manage"
                    :active-value=1
                    :inactive-value=0
                    active-text="Qun l tn kho"
                  >
                  </el-switch>
                </div>
              </div>
              <div class="col-sm-12 col-lg-6 col-md-6 p-0" v-if="checkAddVariant">
                <span class="pull-right btn-edit text-info" @click="dialogFormVariantVisible = true"><i class="el-icon-plus"></i> Thm phin bn</span>
              </div>
            </div>
            <div class="row">
              <div v-for="(item, index) of form.variants" class="col-md-12" style="background-color: aliceblue; margin-top: 10px">
                <div class="row">
                  <div class="col-md-6">
                    <el-switch
                      v-model="item.status"
                      active-value="active"
                      inactive-value="inactive"
                      active-text="To phin bn"
                      :disabled="form.variant_count>1?false:true"
                    >
                    </el-switch>
                  </div>
                  <div class="col-md-6">
                    <span style="width: 100%;">Tn thuc tnh: {{item.attributeTitle}}</span>
                  </div>
                </div>
                <div class="row clearfix">
                  <div class="col-md-6">
                    <span>Tn phin bn</span>
                    <el-input
                      placeholder="Tn phin bn"
                      v-model="item.title"
                      :class="errors.has('title_variant_' + index)?'border-danger':''"
                      v-validate="'required'"
                      :data-vv-name="'title_variant_' + index"
                      data-vv-as="Tn phin bn"
                    ></el-input>
                    <span class="text-danger" v-if="errors.has('title_variant_' + index)">{{ errors.first('title_variant_' + index) }}</span>
                  </div>
                  <div class="col-sm-6">
                    <span>Tn kho:</span>
                    <el-input
                      placeholder="Tn kho"
                      v-model="item.stock_quant"
                      :class="errors.has('stock_variant_' + index)?'border-danger':''"
                      v-validate="'required'"
                      :data-vv-name="'stock_variant_' + index"
                      data-vv-as="Tn kho"
                      :disabled="!stock_manage"
                    ></el-input>
                    <span class="text-danger" v-if="errors.has('stock_variant_' + index)">{{ errors.first('stock_variant_' + index) }}</span>
                  </div>
                </div>
                <div class="row">
                  <div class="col-sm-6">
                    <span>Gi bn:</span>
                    <my-money
                      placeholder="Gi bn"
                      fixed=0
                      v-model="item.price"
                      :class="errors.has('price_variant_' + index)?'border-danger':''"
                      v-validate="'required'"
                      :data-vv-name="'price_variant_' + index"
                      data-vv-as="Gi"
                    >
                      <template slot="append">VN</template>
                    </my-money>
                    <span class="text-danger" v-if="errors.has('price_variant_' + index)">{{ errors.first('price_variant_' + index) }}</span>
                  </div>
                  <div class="col-sm-6">
                    <span>Gi so snh:</span>
                    <my-money
                      placeholder="Gi so snh"
                      fixed=0
                      v-model="item.price_compare"
                    >
                      <template slot="append">VN</template>
                    </my-money>
                  </div>
                </div>
                <div class="row">
                  <div class="col-md-12">
                    <span>Hnh nh</span>
                    <div>
                      <my-image
                        :key="item.id"
                        multiple="true"
                        v-model="item.list_image"
                        :value="item.list_image"
                      ></my-image>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="card" v-if="index == 0 && productAttributes.length">
          <div class="card-header">
            <h6 class="title">Thuc tnh sn phm</h6>
          </div>
          <div class="card-body form-card">
            <div class="row" v-for="item of productAttributes">
              <div class="col-sm-12 col-lg-2 col-md-2">
                <b>{{item.name}}</b>
              </div>
              <div class="col-sm-12 col-lg-10 col-md-10">
                <my-select
                  :attribute="item"
                  :placeholder="item.name"
                  v-model="productAttribute[createHandle(item.name)]"
                  :key="item.name"
                >
                </my-select>
              </div>
            </div>
          </div>
        </div>
      </div>
      <custom-field
        v-if="customField.length > 0"
        :group="customField"
        :value="custom_field"
        v-model="custom_field"
      ></custom-field>
    </div>
    <div class="col-lg-4 col-md-4 col-sm-12">
      <form-card
        v-for="group of sideGroups"
        hasTop="true"
        :group="group"
        v-model="form"
      >
      </form-card>
      <div class="card" v-if="listView">
        <div class="card-header">
          <h6 class="title">Giao din</h6>
        </div>
        <div class="card-body form-card">
          <div class="row">
            <div class="col-sm-12">
              <my-select
                class="full-width"
                :attribute="listView"
                placeholder="Giao din"
                v-model="form.template"
              >
              </my-select>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="container-fluid">
      <div class="page-action">
        <el-button @click="cancel">Hy</el-button>
        <el-button type="primary" @click="save">Cp nht</el-button>
      </div>
    </div>
    <el-dialog title="Thm phin bn" :visible.sync="dialogFormVariantVisible" width="30%" @keyup.enter.native="addVariant">
      <el-form :model="formVariant">
        <el-form-item label="">
          <my-select
            :attribute="listVariantDeleted"
            placeholder="Chn phin bn sn phm"
            v-model="formVariant.variant"
          >
          </my-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVariantVisible = false">ng</el-button>
        <el-button type="primary" @click="addVariantNew">Thm phin bn</el-button>
      </span>
    </el-dialog>
    <el-dialog title="Nhn bn sn phm" :visible.sync="dialogFormDoubleVisible" width="30%" @keyup.enter.native="doubleItem">
      <el-form :model="formDouble">
        <el-form-item label="Tiu  sn phm mi:">
          <el-input v-model="formDouble.title"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogFormDoubleVisible = false">ng</el-button>
        <el-button type="primary" @click="doubleItem">Nhn bn</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import FormCard from 'src/components/UIComponents/FormCard.vue'
import CustomField from 'src/components/UIComponents/CustomField.vue'
import { Input, Button, Switch, Dialog, Form, FormItem, Col, Message, MessageBox } from 'element-ui';
import MyMoney from 'src/components/UIComponents/Money'
import MySelect from 'src/components/UIComponents/Select'
import MyImage from 'src/components/UIComponents/Image'
import dataFrom from './product-form'
import { mapState } from 'vuex'
import util from '@/helpers/util'

let _form = {}, _custom_field = {}, firstGroups, secondGroups;

export default {
  components: {
    FormCard,
    ElInput: Input,
    ElDialog: Dialog,
    ElForm: Form,
    ElCol: Col,
    ElFormItem: FormItem,
    ElButton: Button,
    ElSwitch: Switch,
    MyMoney,
    MySelect,
    MyImage,
    Message,
    CustomField
  },
  beforeCreate() {
    firstGroups = dataFrom[0].groups;
    secondGroups = dataFrom[1].groups;

    if (this.$store.state.customFieldDefine.product){
      _custom_field = this.$store.state.customFieldDefine.product.reduce(function (result, item) {
        result[item.handle] = '';
        return result;
      }, {});
    }

    firstGroups.forEach(group => {
      group.attributes.forEach(attr => {
        if (attr.multiple && _form[attr.prop]) {
          _form[attr.prop] = [];
        } else {
          _form[attr.prop] = '';
        }
        attr.value = _form[attr.prop];
      })
    })
    secondGroups.forEach(group => {
      group.attributes.forEach(attr => {
        if (attr.multiple && _form[attr.prop]) {
          _form[attr.prop] = [];
        } else {
          _form[attr.prop] = '';
        }
        attr.value = _form[attr.prop];
      })
    })

    let id = this.$route.params.id;
    this.$store.dispatch('getCustomField', {
      type: 'product',
      id: id
    });
    this.$store.dispatch('getListThemeView', 'product');
  },
  data () {
    return {
      centerGroups: firstGroups,
      sideGroups: secondGroups,
      stock_manage: true,
      attributes: {},
      productAttribute: {},
      variants: {},
      dialogFormVariantVisible: false,
      formVariant: {},
      listVariantDeleted: {
        options: []
      },
      custom_field: _custom_field,
      dialogFormDoubleVisible: false,
      formDouble: {},
      show_fields: []
    }
  },
  computed: {
    ...mapState(['productDetail', 'productAttributes']),
    form: {
      get () {
        this.formDouble.title = 'Nhn bn ca ' + this.productDetail.title;
        let hashSeo = {};
        if (this.productDetail.seo){
          hashSeo = util.hashFlatten({seo: this.productDetail.seo});
        }
        let _originalForm = Object.assign({}, this.productDetail, hashSeo)
        return _originalForm;
      },
      set (value) {}
    },
    checkAddVariant(){
      let self = this;
      if (self.productDetail.variants_hidden) {
        let variants = self.productDetail.variants_hidden;
        variants.forEach(function (item) {
          item.value = item.id;
        })
        self.listVariantDeleted.options = variants;
        return variants.length;
      }
    },
    customField: {
      get() {
        let data = this.$util.filterByHandle(this.$store.state.customField, this.show_fields);
        for (let item of data) {
          if (item.value) {
            _custom_field[item.handle] = item.value;
          } else {
            _custom_field[item.handle] = '';
          }
        }
        return data||'';
      },
      set(value) {}
    },
    listView() {
      let self = this;
      let listTheme = self.$store.state.listThemeView;
      if (listTheme.length) {
        listTheme.unshift({
          title: 'Mc nh',
          value: ''
        })
        return {
          options: listTheme
        };
      }
      return '';
    }
  },
  mounted() {
    this.getContext();
    let id = this.$route.params.id;
    this.$store.dispatch('fetchProductDetail', id);
    this.$store.dispatch('fetchProductAttributes');

    this.$store.dispatch('setPageTitle', 'Cp nht sn phm');
    this.$store.dispatch('setCurrentActions', [{
      label: 'Nhn bn',
      type: 'default',
      icon: '',
      callback: this.showDouble
    },{
      label: 'Xem trn web',
      type: 'default',
      icon: '',
      callback: this.view
    }, {
      label: 'Xa',
      type: 'warning',
      icon: '',
      callback: this.remove
    }, {
      label: 'Cp nht',
      type: 'primary',
      icon: '',
      callback: this.save
    }])
  },
  methods: {
    showDouble(){
      this.dialogFormDoubleVisible = true;
    },
    doubleItem(){
      let self = this;
      let data = {
        id: self.productDetail.id,
        title: self.formDouble.title
      };
      this.$store.dispatch('doubleProduct', data).then(resp => {
        self.$router.push({path: '/products/' + resp.id});
        location.reload();
        self.dialogFormDoubleVisible = false;
      }, error => {
        Message({
          message: error.message,
          type: 'danger'
        });
      })
    },
    createHandle(value){
      return this.$util.createHandle(value);
    },
    cancel() {
      this.$router.push({name: 'AllProducts'});
    },
    changeFeautureImage(value){
      this.form.image = value;
    },
    updateHandle(){
      let self = this;
      let data = {
        handle: self.$util.createHandle(self.form.title),
        lang: 'vi'
      }
      self.$store.dispatch('checkHandle', data).then(result => {
        self.$store.state.productDetail = self.form;
        self.$store.state.productDetail.handle = result;
      });
    },
    remove(){
      let self = this;
      MessageBox.confirm('Bn c chc chn xa khng?', 'Warning', {
        confirmButtonText: 'ng ',
        cancelButtonText: 'Hy b',
        type: 'warning',
        center: true
      }).then(() => {
        self.$util.updateStatusAll('product', [self.form], 'delete').then(function (result) {
          self.$store.dispatch('fetchProducts');
          Message({
            type: 'success',
            message: 'Xa thnh cng'
          });
          self.$router.push({name: 'AllProducts'});
        }).catch(function (error) {
          Message({
            type: 'error',
            message: error.message
          });
        });
      })
    },
    view(){
      let self = this;
      var win = window.open(self.form.url, '_blank');
      if (win) {
        win.focus();
      } else {
        Message({
          message: 'Please allow popups for this website',
          type: 'danger'
        });
      }
    },
    addVariant(){
      let data = this.formVariant;
      data.product_id = this.form.id;
      data.status = 'active';
      data.option = [];
      data.list_image = [];
      this.$store.dispatch('createVariant', data).then(res => {
        Message({
          message: 'To mi thnh cng',
          type: 'success'
        });
        this.formVariant = {};
        this.dialogFormVariantVisible = false;
      }, res => {
        Message({
          message: res.message,
          type: 'danger'
        });
      });
    },
    addVariantNew(){
      let self = this;
      let variantID = self.formVariant.variant;
      let variantsHidden = self.productDetail.variants_hidden;
      let data = variantsHidden.filter(item => {
        return item.id == variantID;
      })
      if (data.length){
        data[0].status = 'active';
        self.form.variants.push(data[0]);
        self.productDetail.variants_hidden = self.productDetail.variants_hidden.filter(item => {
          return item.id != variantID;
        })
        self.formVariant.variant = '';
        this.dialogFormVariantVisible = false;
      }
    },
    save() {
      let self = this;
      this.$validator.validateAll().then(function (result) {
        if (result) {
          let collections = [];
          if (self.form.objCollection) {
            for (var item in self.form.objCollection) {
              let collection = self.form.objCollection[item];
              collections.push(collection.id);
            }
          }
          self.form.collections = collections;
          self.form.listImage = self.form.list_image.map(item => { return item.name});
          if (!self.form.image){
            self.form.image = self.form.list_image?self.form.listImage[0]:'';
          }
          self.$store.dispatch('updateProduct', self.form).then(res => {
            self.$store.dispatch('storeCustomField', self.$util.parseDataCustomfield(self.customField, self.custom_field, 'product', self.form.id));

            Message({
              message: 'Cp nht thnh cng',
              type: 'success'
            });
          }, res => {
            Message({
              message: res.message,
              type: 'danger'
            });
          });
        }
      })
    },
    getContext() {
      let ctx = this.$util.getContext(this.form, {module: 'products'});
      this.$store.state.ctx = ctx;
      return ctx;
    },
  },
  watch: {
    'form.stock_manage'(newVal, oldVal) {
      this.stock_manage = newVal;
    },
    productAttributes(newVal, oldVal){
      this.productAttributes.forEach(function (productAttribute) {
        productAttribute.multiple = true;
        productAttribute.child.forEach(function (item) {
          item.title = item.name;
          item.value = item.name;
        })
        productAttribute.options = productAttribute.child;
      })
    },
    'form.product_attribute'(newVal, oldVal){
      this.productAttribute = this.form.product_attribute;
    },
    'form': {
      handler(newVal, oldVal) {
        let ctx = this.getContext();
        let output = window.__FUNC.CustomField(ctx);
        this.show_fields = output;
      },
      deep: true
    }
  },
  destroyed(){
    this.$store.dispatch('setCurrentActions', []);
    this.$store.state.productAttributes = [];
    this.$store.state.productDetail = [];
    this.$store.state.listThemeView = [];
    this.$store.state.customField = [];
  }
}
</script>
